import React from 'react'
import '../App.css';
import data from './data/videoPge';
import VideoSection from './page3/VideoSection';

  const tempArr = [];
  const tempData = [...data]
  for(let i = 0; i<=3; i++){
    tempArr[i] = [];
    for(let j = 0 ; j<=5 ; j++){
      tempArr[i].push(tempData[0]);
      tempData.splice(0,1)
    }
  }

export default function Page3() {
    return (
        <div className="page3">
           {
             
            tempArr.map((arr)=>{
              if(arr !== 'undefined'){
              return(
                <VideoSection mainSrc = {arr}/>
              )
              }
            })
          }
        </div>
                    )}

    
          
          
