import React from 'react'
import '../../App.css';
import VideoCard from './VideoCard';
import VideoFrame from './VideoFrame';

export default function VideoSection({mainSrc}) {
      
    return (
        <div className="video-page-tiles">
         
            {
        mainSrc.map((obj, index)=>{
            
            return(
                index === 0 ?
                <VideoFrame src = {obj.link}/> :
                    <VideoCard dataSrc = {obj.link} image = {obj.img}/>
            )
        
        })
    }
        

    </div>
                )
        }
